@import url("https://fonts.googleapis.com/css2?family=Open+Sans:wght@300&family=Roboto:ital@1&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Archivo&display=swap");
.App {
  background-image: url("../../images/UEFAbg123.jpeg");
  height: 100vh;
  position: relative;
}
.History {
  margin-left: 200px;
  padding-top: 200px;
  margin-right: 200px;
}
.DisplayColumn {
  display: grid;
  grid-template-columns: 3.4fr 1fr;
  column-gap: 40px;
}
.DivVideo {
  height: 600px;
  width: 100%;
}

.DivVide iframe {
  width: 100%;
  height: 100%;
  position: relative;
}
.DivStats {
  background-color: white;
  width: 100%;
  height: 380px;
  border: 1px solid rgb(231, 225, 225);
}
.DivStatsDisplay {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 0px;
}
.DisplayColumn1 {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
}
.DisplayColumn2 {
  display: grid;
  grid-template-columns: 1fr 3fr 1fr;
}
.DisplayColumn2 img {
  height: 50px;
  width: 50px;
}
.Text1 {
  margin-top: 4px;
  font-family: "Archivo", sans-serif;
  font-size: 32px;
  font-weight: 600;
  color: #0c6fd2ec;
  margin-bottom: 0px;
}
.Text2 {
  margin-top: 30px;
  font-family: "Archivo", sans-serif;
  font-size: 30px;
  font-weight: 550;
  color: #0c6fd2ec;
}
.DisplayColumn11 {
  padding-left: 20px;
  padding-right: 20px;
  padding-top: 20px;
  padding-bottom: 20px;
  display: grid;
  grid-template-columns: repeat(1, 1fr);
}
